import { Component, OnInit, MissingTranslationStrategy } from "@angular/core";
import { MypropertiesService } from "./myproperties.service";

@Component({
  selector: "app-myproperties", 
  templateUrl: "./myproperties.component.html",
  styleUrls: ["./myproperties.component.css"]
})
export class MypropertiesComponent implements OnInit {
  uid: any;
  user: any;
  propertyDetails = [];
  constructor(private myproperty_service: MypropertiesService) {}

  ngOnInit() {
    // User ID
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    // Fetch details
    this.myproperty_service.getSellerProperties(this.uid).then(res => {
      res.forEach(element => {
        this.propertyDetails.push(element.data());
      });
      console.log(this.propertyDetails);
    });
  }
}
