import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-ownaproperty",
  templateUrl: "./ownaproperty.component.html",
  styleUrls: ["./ownaproperty.component.css", "../common.css"]
})
export class OwnapropertyComponent implements OnInit {
  useCase: any;
  activeTab: any;
  constructor(private route: ActivatedRoute) {}

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.useCase = params["use"];
      if (this.useCase == "buy") {
        this.activeTab = 0;
      } else if (this.useCase == "sell") {
        this.activeTab = 1;
      }
      console.log(this.useCase);
    });
  }
}
