import { Component, OnInit } from "@angular/core";
import { MyrequirementService } from "./myrequirement.service";

@Component({
  selector: "app-myrequirement",
  templateUrl: "./myrequirement.component.html",
  styleUrls: ["./myrequirement.component.css"]
})
export class MyrequirementComponent implements OnInit {
  uid: any;
  user: any;
  propertyDetails = [];
  constructor(private myrequirement_service: MyrequirementService) {}

  ngOnInit() {
    // User ID
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    // Fetch details
    this.myrequirement_service.getBuyerRequirement(this.uid).then(res => {
      res.forEach(element => {
        console.log(element.data());
        this.propertyDetails.push(element.data());
      });
    });
  }
}
