import { Component, OnInit } from '@angular/core';
import {SelectedpropertydetailService} from './selectedpropertydetail.service'
import { StateServiceService } from "../state-service.service";

import {SelectedpropertydialogComponent} from '../selectedpropertydialog/selectedpropertydialog.component';
import { VERSION, MatDialogRef, MatDialog, MatSnackBar, MAT_DIALOG_DATA } from '@angular/material';
@Component({
  selector: 'app-selectedpropertydetail',
  templateUrl: './selectedpropertydetail.component.html',
  styleUrls: ['./selectedpropertydetail.component.css']
})
export class SelectedpropertydetailComponent implements OnInit {
  user: any;
  uid: any;
  return: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  propertyDetails = [];
  version = VERSION;
  constructor(
    private  SelectedpropertydetailService:  SelectedpropertydetailService,
    private stateService: StateServiceService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar
    ) { }

   
     
  ngOnInit() {
    
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    // Fetch details
    this.SelectedpropertydetailService.getSellerProperties(this.uid).then(res => {
      res.forEach(element => {
        this.propertyDetails.push(element.data());
      });
      console.log(this.propertyDetails);
    });
  
  }




  

openAlertDialog() {
  const dialogRef = this.dialog.open(SelectedpropertydialogComponent,{
    data:{
      message: 'HelloWorld',
      buttonText: {
        cancel: 'Done'
      }
    },
  });
}
}