import {Component,ViewChild,AfterViewInit, OnDestroy, OnInit} from "@angular/core";
import { AuthService } from "../auth.service";
import { Buyer } from "./forms";
import { FormsService } from "./forms.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { FormControl, Validators, FormGroup } from "@angular/forms";
import {VERSION,MatDialogRef, MatDialog, MatSnackBar,MAT_DIALOG_DATA,MatAutocompleteSelectedEvent,MatAutocompleteTrigger} from "@angular/material";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { StateServiceService } from "../state-service.service";
import { Observable } from "rxjs";
import { map, startWith } from "rxjs/operators";
import {AngularFirestore,AngularFirestoreDocument} from "@angular/fire/firestore";
import { Config, HttpService } from "../http.service";
declare var $: any;
import { debounceTime } from "rxjs/operators";
import { switchMap } from "rxjs/operators";
import { of } from "rxjs";

@Component({
  selector: "app-forms",
  templateUrl: "./forms.component.html",
  styleUrls: ["./forms.component.css"]
})
export class FormsComponent implements OnInit {
  public addressianAutoComplete$: Observable<any> = null;
  public autoCompleteControl = new FormControl();
  data: any;
  isLoggedIn: Boolean = true;
  userData: any;
  config: Config;
  Buyer: Buyer = new Buyer();
  submitted = false;
  DatafieldsService: any;
  financial: any[];
  links = ["First", "Second", "Third"];
  activeLink = this.links[0];
  background = "";
  selectedIndex = 0;
  maxNumberOfTabs = 2;
  isLoading: boolean = false;
  return: any;
  obj: any;
  version = VERSION;
  form: FormGroup;
  signatureFormGroup: any;

  myControl = new FormControl();
  result: any;
  selectIte: any;

  toggleBackground() {
    this.background = this.background ? "" : "primary";
  }

  addLink() {
    this.links.push(`Link ${this.links.length + 1}`);
  }

  constructor(
    public authService: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    private formsService: FormsService,
    private firestore: AngularFirestore,
    public afAuth: AngularFireAuth,
    db: AngularFirestore,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private router: Router,
    private stateService: StateServiceService,
    private postcodeService: HttpService
  ) {}

  ngOnInit() {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        this.LoggedOut();
      }
    });
    // this.filteredOptions = this.myControl.valueChanges.pipe(
    //   startWith(""),
    //   map(value => (typeof value === "string" ? value : value.postcode)),
    //   map(postcode =>
    //     postcode ? this._filter(postcode) : this.options.slice()
    //   )
    // );

    // The auto population of github method
    this.addressianAutoComplete$ = this.autoCompleteControl.valueChanges.pipe(
      startWith(""),
      // delay emits
      debounceTime(1000),
      // use switch map so as to cancel previous subscribed events, before creating new once
      switchMap(value => {
        if (value !== "") {
          this.lookup(this.Buyer.postcode).subscribe(data => {
            this.data = data;
          });

          return this.lookup(this.Buyer.postcode);
        } else {
          return of(null);
        }
      })
    );
    // End method
  }

  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }

  newCustomer(): void {
    this.submitted = false;
    this.Buyer = new Buyer();
  }

  onSubmit() {
    this.stateService.Buyer = this.Buyer;
    this.router.navigate(["buyerdetails"]);
  }

  emailFormControl = new FormControl("", [
    Validators.required,
    Validators.email
  ]);

  lastnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  firstnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  AddressFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  TownFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  stateFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  postcodeFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  addressnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(6)
  ]);

  lookup(value): Observable<any> {
    return this.postcodeService.search(value);
  }

  getPosts(value) {
    this.Buyer.Address = value.address;
    this.Buyer.Town = value.citytown;
    this.Buyer.state = value.county;
    this.Buyer.postcode = value.postcode;
    console.log(value);
  }
}
