import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { SellerformService } from "../sellerform/sellerform.service";
import { AuthService } from "../auth.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { StateServiceService } from "../state-service.service";
import { Seller } from "../sellerform/seller";
import { VERSION, MatDialogRef, MatDialog, MatSnackBar, MAT_DIALOG_DATA } from '@angular/material';
import { AlertDialogComponent } from '../alert-dialog/alert-dialog.component';
import {Location} from '@angular/common';

@Component({
  selector: "app-confirmation",
  templateUrl: "./confirmation.component.html",
  styleUrls: ["./confirmation.component.css"]
})
export class ConfirmationComponent implements OnInit {
  return: any;
  userData: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  Seller: Seller;
  version = VERSION; 
  constructor(
    private activatedRoute: ActivatedRoute,
    private SellerformService: SellerformService,
    public authService: AuthService,
    public afAuth: AngularFireAuth,
    private stateService: StateServiceService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private _location: Location
  ) {}

  ngOnInit() {
    // Auth
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        this.LoggedOut();
      }
    });

    this.Seller = this.stateService.Seller;
  }

  submitForm() {
    this.isLoading = true;
    this.return = this.SellerformService.createCustomer(
      this.userData.uid,
      this.Seller
    ).then(data => {
      if (data == true) {
        this.isLoading = false;
        const dialogRef = this.dialog.open(AlertDialogComponent,{
          data:{
            message: 'HelloWorld',
            buttonText: {
              cancel: 'Done'
            }
          },
        });
      }
    });
  }

  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }
  backClicked() {
    this._location.back();
  }
}
