import { Injectable, EventEmitter } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";
import { Buyer } from "./forms";

@Injectable({
  providedIn: "root"
})
export class FormsService {
  private dbPath = "Buyer";
  dataSavedEventEmitter = new EventEmitter<string>();
  return: any;

  customersRef: AngularFirestoreCollection<Buyer> = null;
  constructor(private db: AngularFirestore) {
    this.customersRef = db.collection(this.dbPath);
  }
  async createCustomer(key, customer: Buyer) {
    this.return = await this.customersRef
      .doc(key)
      .collection("requirements")
      .add({ ...customer })
      .then(function(data) {
        console.log("Document successfully written!");
      });
    return true;
  }

  updateCustomer(key: string, value: any): Promise<void> {
    return this.customersRef.doc(key).update(value);
  }

  deleteCustomer(key: string): Promise<void> {
    return this.customersRef.doc(key).delete();
  }

  getCustomersList(): AngularFirestoreCollection<Buyer> {
    return this.customersRef;
  }

  deleteAll() {
    this.customersRef.get().subscribe(
      querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.delete();
        });
      },
      error => {
        console.log("Error: ", error);
      }
    );
  }
}
