import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { FormsService } from "../forms/forms.service";
import { AuthService } from "../auth.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { StateServiceService } from "../state-service.service";
import { Buyer } from "../forms/forms";
import { VERSION, MatDialogRef, MatDialog, MatSnackBar, MAT_DIALOG_DATA } from '@angular/material';
import { AlertDialogComponent } from '../alert-dialog/alert-dialog.component';
import {AlertdialogbuyerComponent} from '../alertdialogbuyer/alertdialogbuyer.component';
import {Location} from '@angular/common';

@Component({
  selector: "app-buyerdetails",
  templateUrl: "./buyerdetails.component.html",
  styleUrls: ["./buyerdetails.component.css"]
})
export class BuyerdetailsComponent implements OnInit {
  return: any;
  userData: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  Buyer: Buyer;
  selectedIndex = 0;
  version = VERSION;

  maxNumberOfTabs = 2;
  
  routerExtensions: any;

  constructor(
    private activatedRoute: ActivatedRoute,
    private formsService: FormsService,
    public authService: AuthService,
    public afAuth: AngularFireAuth,
    private stateService: StateServiceService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private _location: Location
  ) {}

  ngOnInit() {
    // Auth
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        this.LoggedOut();
      }
    });

    this.Buyer = this.stateService.Buyer;
    
  }

  submitForm() {
    this.isLoading = true;
    this.return = this.formsService.createCustomer(
      this.userData.uid,
      this.Buyer
    ).then(data => {
      if (data == true) {
        this.isLoading = false;
        const dialogRef = this.dialog.open(AlertdialogbuyerComponent,{
          data:{
            message: 'HelloWorld',
            buttonText: {
              cancel: 'Done'
            }
          },
        });
      }
    });
  }

  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }
  

  backClicked() {
    this._location.back();
  }
}
