import { Injectable } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";
import { Seller } from "./seller";

@Injectable({
  providedIn: "root"
})
export class SellerformService {
  private dbPath = "/Seller";

  customersRef: AngularFirestoreCollection<Seller> = null;
  return: void;
  constructor(private db: AngularFirestore) {
    this.customersRef = db.collection(this.dbPath);
  }

  async createCustomer(key, customer: Seller) {
    this.return = await this.customersRef
      .doc(key)
      .collection("properties")
      .add({ ...customer })
      .then(function(data) {
        console.log("Document successfully written!");
      });
    return true;
  }

  updateCustomer(key: string, value: any): Promise<void> {
    return this.customersRef.doc(key).update(value);
  }

  deleteCustomer(key: string): Promise<void> {
    return this.customersRef.doc(key).delete();
  }

  getCustomersList(): AngularFirestoreCollection<Seller> {
    return this.customersRef;
  }

  deleteAll() {
    this.customersRef.get().subscribe(
      querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.delete();
        });
      },
      error => {
        console.log("Error: ", error);
      }
    );
  }
}
